<template><div class="markdown-body"><h1>时间输入框</h1>
<p><code v-pre="">mu-date-input</code> 用于选择日期。将 <code v-pre="">mu-text-field</code>、<code v-pre="">mu-date-picker</code> 和 <code v-pre="">mu-time-picker</code> 组合在一起形成一个新的组件。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row gutter&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input v-model=\&quot;value1\&quot; label=\&quot;选择日期\&quot; label-float=\&quot;\&quot; full-width&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input v-model=\&quot;value2\&quot; label=\&quot;横屏显示\&quot; label-float=\&quot;\&quot; full-width=\&quot;\&quot; landscape&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input v-model=\&quot;value3\&quot; label=\&quot;隐藏日期展示\&quot; label-float=\&quot;\&quot; full-width=\&quot;\&quot; no-display&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      value1: undefined,\n      value2: undefined,\n      value3: undefined\n    };\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input v-model="value1" label="选择日期" label-float="" full-width=""></mu-date-input>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input v-model="value2" label="横屏显示" label-float="" full-width="" landscape=""></mu-date-input>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input v-model="value3" label="隐藏日期展示" label-float="" full-width="" no-display=""></mu-date-input>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
