      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选择日期"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">full-width</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"12"</span> <span class="hljs-attr">lg</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">sm</span>=<span class="hljs-string">"6"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"横屏显示"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">full-width</span> <span class="hljs-attr">landscape</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-input</span>&gt;</span>
