<h2 id="bu-tong-de-xian-shi-fang-shi"><a class="header-anchor" href="#bu-tong-de-xian-shi-fang-shi" aria-hidden="true">¶</a> 不同的显示方式</h2>
<p>改变 <code v-pre="">container</code> 属性改变不同的弹出方式，目前有 <code v-pre="">popover</code>, <code v-pre="">dialog</code>, <code v-pre="">bottomSheet</code> 三种方式</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row gutter&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input icon=\&quot;today\&quot; v-model=\&quot;value4\&quot; label=\&quot;popover 显示\&quot; label-float=\&quot;\&quot; full-width&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input icon=\&quot;today\&quot; v-model=\&quot;value5\&quot; label=\&quot;对话框显示\&quot; container=\&quot;dialog\&quot; label-float=\&quot;\&quot; full-width&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input icon=\&quot;today\&quot; v-model=\&quot;value6\&quot; label=\&quot;底部弹出显示\&quot; container=\&quot;bottomSheet\&quot; label-float=\&quot;\&quot; full-width&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      value4: undefined,\n      value5: undefined,\n      value6: undefined\n    };\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" v-model="value4" label="popover 显示" label-float="" full-width=""></mu-date-input>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" v-model="value5" label="对话框显示" container="dialog" label-float="" full-width=""></mu-date-input>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" v-model="value6" label="底部弹出显示" container="bottomSheet" label-float="" full-width=""></mu-date-input>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
