      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-input</span>  <span class="hljs-attr">icon</span>=<span class="hljs-string">"today"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"对话框显示"</span> <span class="hljs-attr">container</span>=<span class="hljs-string">"dialog"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">full-width</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-input</span>&gt;</span>
