    </mu-col>
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" landscape="" v-model="value8" label="选择时间" type="time" label-float="" full-width=""></mu-date-input>
    </mu-col>
    <mu-col span="12" lg="4" sm="6">
