    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" v-model="value11" label="选择日期和时间" type="dateTime" label-float="" full-width="" landscape=""></mu-date-input>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
