    <span class="hljs-keyword">const</span> maxDate = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>();
    minDate.setDate(<span class="hljs-number">1</span>);
    maxDate.setMonth(maxDate.getMonth() + <span class="hljs-number">1</span>);
    maxDate.setDate(<span class="hljs-number">0</span>);
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value12</span>: <span class="hljs-literal">null</span>,
      <span class="hljs-attr">value13</span>: <span class="hljs-literal">null</span>,
      <span class="hljs-attr">value14</span>: <span class="hljs-literal">null</span>,
      <span class="hljs-attr">value15</span>: <span class="hljs-literal">null</span>,
      <span class="hljs-attr">value16</span>: <span class="hljs-literal">null</span>,
      <span class="hljs-attr">value17</span>: <span class="hljs-literal">null</span>,
      minDate,
      maxDate
    }
  },
  <span class="hljs-attr">methods</span>: {
    disableWeekends (date) {
      <span class="hljs-keyword">return</span> date.getDay() === <span class="hljs-number">0</span> || date.getDay() === <span class="hljs-number">6</span>
    },
