<h2 id="bu-tong-yu-yan-huan-jing"><a class="header-anchor" href="#bu-tong-yu-yan-huan-jing" aria-hidden="true">¶</a> 不同语言环境</h2>
<p>通过自定义 <code v-pre="">date-time-format</code> 来格式化不同语言的显示</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-row gutter&gt;\n    &lt;mu-col span=\&quot;12\&quot; lg=\&quot;4\&quot; sm=\&quot;6\&quot;&gt;\n      &lt;mu-date-input icon=\&quot;today\&quot; v-model=\&quot;value18\&quot; label=\&quot;English\&quot; label-float=\&quot;\&quot; full-width :date-time-format=\&quot;enDateFormat\&quot;&gt;&lt;/mu-date-input&gt;\n    &lt;/mu-col&gt;\n  &lt;/mu-row&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nconst dayAbbreviation = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];\nconst dayList = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];\nconst monthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep',\n  'Oct', 'Nov', 'Dec'];\nconst monthLongList = ['January', 'February', 'March', 'April', 'May', 'June',\n  'July', 'August', 'September', 'October', 'November', 'December'];\n\nconst enDateFormat = {\n  formatDisplay (date) {\n    return `${dayList[date.getDay()]}, ${monthList[date.getMonth()]} ${date.getDate()}`;\n  },\n  formatMonth (date) {\n    return `${monthLongList[date.getMonth()]} ${date.getFullYear()}`;\n  },\n  getWeekDayArray (firstDayOfWeek) {\n    let beforeArray = [];\n    let afterArray = [];\n    for (let i = 0; i &lt; dayAbbreviation.length; i++) {\n      if (i &lt; firstDayOfWeek) {\n        afterArray.push(dayAbbreviation[i]);\n      } else {\n        beforeArray.push(dayAbbreviation[i]);\n      }\n    }\n    return beforeArray.concat(afterArray);\n  },\n  getMonthList () {\n    return monthList;\n  }\n};\n\nexport default {\n  data () {\n    return {\n      value18: undefined,\n      enDateFormat\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-row gutter="">
    <mu-col span="12" lg="4" sm="6">
      <mu-date-input icon="today" v-model="value18" label="English" label-float="" full-width="" :date-time-format="enDateFormat"></mu-date-input>
    </mu-col>
  </mu-row>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-row</span> <span class="hljs-attr">gutter</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-col</span> <span class="hljs-attr">span</span>=<span class="hljs-string">"12"</span> <span class="hljs-attr">lg</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">sm</span>=<span class="hljs-string">"6"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-date-input</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"today"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value18"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"English"</span> <span class="hljs-attr">label-float</span> <span class="hljs-attr">full-width</span> <span class="hljs-attr">:date-time-format</span>=<span class="hljs-string">"enDateFormat"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-date-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-row</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">const</span> dayAbbreviation = [<span class="hljs-string">'S'</span>, <span class="hljs-string">'M'</span>, <span class="hljs-string">'T'</span>, <span class="hljs-string">'W'</span>, <span class="hljs-string">'T'</span>, <span class="hljs-string">'F'</span>, <span class="hljs-string">'S'</span>];
<span class="hljs-keyword">const</span> dayList = [<span class="hljs-string">'Sun'</span>, <span class="hljs-string">'Mon'</span>, <span class="hljs-string">'Tue'</span>, <span class="hljs-string">'Wed'</span>, <span class="hljs-string">'Thu'</span>, <span class="hljs-string">'Fri'</span>, <span class="hljs-string">'Sat'</span>];
<span class="hljs-keyword">const</span> monthList = [<span class="hljs-string">'Jan'</span>, <span class="hljs-string">'Feb'</span>, <span class="hljs-string">'Mar'</span>, <span class="hljs-string">'Apr'</span>, <span class="hljs-string">'May'</span>, <span class="hljs-string">'Jun'</span>, <span class="hljs-string">'Jul'</span>, <span class="hljs-string">'Aug'</span>, <span class="hljs-string">'Sep'</span>,
  <span class="hljs-string">'Oct'</span>, <span class="hljs-string">'Nov'</span>, <span class="hljs-string">'Dec'</span>];
<span class="hljs-keyword">const</span> monthLongList = [<span class="hljs-string">'January'</span>, <span class="hljs-string">'February'</span>, <span class="hljs-string">'March'</span>, <span class="hljs-string">'April'</span>, <span class="hljs-string">'May'</span>, <span class="hljs-string">'June'</span>,
  <span class="hljs-string">'July'</span>, <span class="hljs-string">'August'</span>, <span class="hljs-string">'September'</span>, <span class="hljs-string">'October'</span>, <span class="hljs-string">'November'</span>, <span class="hljs-string">'December'</span>];

<span class="hljs-keyword">const</span> enDateFormat = {
  formatDisplay (date) {
    <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${dayList[date.getDay()]}</span>, <span class="hljs-subst">${monthList[date.getMonth()]}</span> <span class="hljs-subst">${date.getDate()}</span>`</span>;
  },
  formatMonth (date) {
    <span class="hljs-keyword">return</span> <span class="hljs-string">`<span class="hljs-subst">${monthLongList[date.getMonth()]}</span> <span class="hljs-subst">${date.getFullYear()}</span>`</span>;
  },
  getWeekDayArray (firstDayOfWeek) {
    <span class="hljs-keyword">let</span> beforeArray = [];
    <span class="hljs-keyword">let</span> afterArray = [];
    <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; dayAbbreviation.length; i++) {
      <span class="hljs-keyword">if</span> (i &lt; firstDayOfWeek) {
        afterArray.push(dayAbbreviation[i]);
      } <span class="hljs-keyword">else</span> {
        beforeArray.push(dayAbbreviation[i]);
      }
    }
    <span class="hljs-keyword">return</span> beforeArray.concat(afterArray);
  },
  getMonthList () {
    <span class="hljs-keyword">return</span> monthList;
  }
};

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value18</span>: <span class="hljs-literal">undefined</span>,
      enDateFormat
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<p><mu-alert color="info">mu-date-input 支持 input 标签所有的原生属性和事件。</mu-alert></p>
<h2 id="date-input-props"><a class="header-anchor" href="#date-input-props" aria-hidden="true">¶</a> Date Input props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>container</td>
<td>弹出方式</td>
<td>String</td>
<td>popover/dialog/bottomSheet</td>
<td>popover</td>
</tr>
<tr>
<td>type</td>
<td>选择的类型</td>
<td>String</td>
<td>date/time/year/month/dateTime</td>
<td>date</td>
</tr>
<tr>
<td>clock-type</td>
<td>时间选择器显示类型</td>
<td>String</td>
<td>ampm/24hr</td>
<td>ampm</td>
</tr>
<tr>
<td>format</td>
<td>显示在输入框中的格式</td>
<td>String</td>
<td>—</td>
<td>yyyy—MM-dd</td>
</tr>
<tr>
<td>actions</td>
<td>是否显示底部的确定/取消按钮</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>ok-label</td>
<td>确定按钮的文本</td>
<td>String</td>
<td>—</td>
<td>确定</td>
</tr>
<tr>
<td>cancel-label</td>
<td>取消按钮的文本</td>
<td>String</td>
<td>—</td>
<td>取消</td>
</tr>
<tr>
<td>value</td>
<td>用于 <code v-pre="">v-model</code> 绑定</td>
<td>Date, String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>color</td>
<td>输入框颜色</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>icon</td>
<td>输入框左边的图标</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label-float</td>
<td>标签是否浮动</td>
<td>Boolean</td>
<td>—</td>
